@mixin responsive($size) {
  @if $size == "sm" {
    @media (max-width: 600px) {
      @content;
    }
  }

  @if $size == "md" {
    @media (min-width: 600px) {
      @content;
    }
  }

  @if $size == "lg" {
    @media (min-width: 992px) {
      @content;
    }
  }
}

@mixin transition-fast {
  transition: 0.3s ease;
}

@mixin flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}